<template>
  <div class="hello">
      <ul>
        <li class="li-tab" v-for="(item,index) in tabsParam"
            @click="toggleTabs(index)"
            :class="{active:index===nowIndex}">{{item}}
      </li>
      </ul>
      <aa v-show="nowIndex===0"></aa>
      <bb v-show="nowIndex===1"></bb>
  </div>
</template>

<script>
import aa from './a.vue';
import bb from './b.vue';

export default {
	name: 'HelloWorld',
	data(){
		return{
			tabsParam:['aa','bb'],
			nowIndex:0,
			isShow:false,
		}
	},
	props: {
		msg: String
	},
	components:{
		aa,bb
	},
	methods:{
		//切换tab项
		toggleTabs(index){
			this.nowIndex = index;
		},
	}
}
</script>
